import { createSlice } from "@reduxjs/toolkit";

export const stepSlice = createSlice({
  name: "step",
  initialState: {
    value: 0,
  },
  reducers: {
    resetStep: (state) => {
      state.value = 0;
    },
    inputStep: (state) => {
      state.value = 1;
    },
    connectStep: (state) => {
      state.value = 2;
    },
  },
});

export const { resetStep, inputStep, connectStep } = stepSlice.actions;

export const selectStep = (state) => state.step.value;

export default stepSlice.reducer;
