import { createSlice } from "@reduxjs/toolkit";

export const transactionSlice = createSlice({
  name: "transaction",
  initialState: {
    details: {
      state: false,
      time: "0",
      sender: "",
      token: "",
      recipient: "",
      amount: 0,
    },
  },
  reducers: {
    resetTx: (state) => {
      state.details = {
        state: false,
        time: 0,
        sender: "",
        token: "",
        recipient: "",
        amount: 0,
      };
    },
    setTx: (state, action) => {
      state.details = action.payload;
    },
  },
});

export const { resetTx, setTx } = transactionSlice.actions;

export const selectTransaction = (state) => state.transaction.details;

export default transactionSlice.reducer;
