import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setTx } from "../../../store/reducers/transactionSlice";
import { selectAccounts } from "../../../store/reducers/accountsSlice";
import { tokenTransfer } from "../../../utils/tokenTransfer";
import { addressFormater } from "../../../helpers/addressFormater";
import { getCurrentDateTime } from "../../../helpers/getCurrentDateTime";
import Button from "../../../components/buttons/Button";
import { TransactionSuccessModal } from "../../../components/modals/TransactionSuccessModal";
import { TokenReceivedModal } from "../../../components/modals/TokenReceivedModal";
import { TransactionFailedModal } from "../../../components/modals/TransactionFailedModal";
import "./style.css";

const SendToken = ({ setIsSendClicked, setIsTxLoading }) => {
  useEffect(() => {
    setIsTxLoading(false);
  }, []);
  const accounts = useSelector(selectAccounts);
  const dispatch = useDispatch();

  const [sendSetting, setSendSetting] = useState({
    sender: 0,
    mint: 0,
    receiver: "",
    amount: Number(0),
  });

  const [showSuccessModal, setShowSuccessModal] = useState(false);
  const [showFailedModal, setShowFailedModal] = useState(false);
  const [showReceivedModal, setShowReceivedModal] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();

    setIsTxLoading(true);
    const fromWallet = accounts[sendSetting.sender].keypair;
    // const fromAddress = accounts[sendSetting.sender].pubkey;
    const toAddress = sendSetting.receiver;
    const mint = accounts[sendSetting.sender].tokens[sendSetting.mint].mint;
    const amount = sendSetting.amount;

    const transactionInfo = await tokenTransfer(
      fromWallet,
      // fromAddress,
      toAddress,
      mint,
      amount
    )
      .then((response) => response)
      .catch((err) => console.error(err));

    setIsTxLoading(false);
    if (transactionInfo === "Error") {
      setShowFailedModal(true);
    } else {
      setShowSuccessModal(true);
      const txDetails = {
        state: true,
        time: getCurrentDateTime(),
        sender: accounts[sendSetting.sender].pubkey,
        token: accounts[sendSetting.sender].tokens[sendSetting.mint].name,
        recipient: toAddress,
        amount: amount,
      };
      dispatch(setTx(txDetails));
      setIsSendClicked(true);
    }
  };

  return (
    <div className="sendToken bg-darkpurple">
      <div className="ft-big">Send</div>
      <form onSubmit={handleSubmit}>
        <div className="operation row">
          <div className="col-3">
            <div className="ft-small">Account ID</div>
            <select
              value={sendSetting.sender}
              onChange={(e) =>
                setSendSetting({ ...sendSetting, sender: e.target.value })
              }
            >
              {accounts.map((v, i) => (
                <option value={i} key={i}>
                  {addressFormater(v?.pubkey)}
                </option>
              ))}
            </select>
          </div>
          <div className="col-3">
            <div className="ft-small">Token</div>
            <select
              value={sendSetting.mint}
              onChange={(e) =>
                setSendSetting({ ...sendSetting, mint: e.target.value })
              }
            >
              {accounts[sendSetting.sender]?.tokens.map((token, i) => (
                <option value={i} key={i}>
                  {token.name}
                </option>
              ))}
            </select>
          </div>
          <div className="col-3">
            <div className="ft-small">Recipient</div>
            <input
              placeholder="Insert Recipient ID"
              onChange={(e) =>
                setSendSetting({ ...sendSetting, receiver: e.target.value })
              }
              required
            />
          </div>
          <div className="col-3">
            <div className="ft-small">Amount</div>
            <input
              placeholder="Insert Value"
              onChange={(e) =>
                setSendSetting({
                  ...sendSetting,
                  amount: Number(e.target.value),
                })
              }
              required
            />
          </div>
        </div>
        <div className="button-area">
          <Button type="submit">Send</Button>
        </div>
      </form>
      <div className="transactionDetails">
        <TransactionSuccessModal
          showSuccessModal={showSuccessModal}
          setShowSuccessModal={setShowSuccessModal}
          setShowReceivedModal={setShowReceivedModal}
        />
        {/* <TokenReceivedModal
          showReceivedModal={showReceivedModal}
          setShowReceivedModal={setShowReceivedModal}
        /> */}

        <TransactionFailedModal
          showFailedModal={showFailedModal}
          setShowFailedModal={setShowFailedModal}
        />
      </div>
    </div>
  );
};

export default SendToken;
