import { useSelector } from "react-redux";
import { selectAccounts } from "../../../store/reducers/accountsSlice";
import { addressFormater } from "../../../helpers/addressFormater";
import "./style.css";

const WalletInfo = () => {
  const accounts = useSelector(selectAccounts);

  return (
    <div className="walletInfo bg-darkpurple">
      <div className="main-account">
        <span className="title">Main Account ID</span>
        <div className="address-area ft-big">
          <p className="address">
            {accounts.length !== 0 ? addressFormater(accounts[0].pubkey) : ""}
          </p>
          <span className="copy" />
        </div>
      </div>
      <div className="accounts d-flex">
        <div className="width-30">Account</div>
        <div>Assets</div>
      </div>
      <div className="table">
        <div className="thead">
          <div className="tr ft-small">
            <div>ID</div>
            <div>Name</div>
            <div>Quantity</div>
          </div>
        </div>
        <div className="tbody">
          {accounts.map((account) =>
            account.tokens.map((token, i) => (
              <div key={i} className="tr">
                <div>{addressFormater(account.pubkey)}</div>
                <div>{token.name}</div>
                <div>{token.amount}</div>
              </div>
            ))
          )}
        </div>
      </div>
    </div>
  );
};

export default WalletInfo;
