import { TOKEN_PROGRAM_ID } from "@solana/spl-token";
import { clusterApiUrl, Connection, PublicKey } from "@solana/web3.js";
import { TokenListProvider, ENV } from "@solana/spl-token-registry";
import {
  SOL_DECIMALS,
  WrappedSOL,
  WalletToken,
  OKToGoToken,
} from "../constants";

const connection = new Connection(clusterApiUrl("devnet"), "confirmed");

async function getSolBalance(publicKey) {
  const balance = await connection.getBalance(publicKey);
  return balance / Math.pow(10, SOL_DECIMALS);
}

export const getAccountInfo = async (account) => {
  const keypair = account.keypair;
  const pubkey = account.pubkey;

  const tokens = [];
  const accounts = await connection.getParsedProgramAccounts(
    new PublicKey(TOKEN_PROGRAM_ID),
    {
      filters: [
        {
          dataSize: 165, // Size of the account data for SPL Token accounts
        },
        {
          memcmp: {
            offset: 32, // Offset of the owner public key in the account data
            bytes: pubkey, // Base58-encoded public key of the wallet
          },
        },
      ],
    }
  );

  const balance = await getSolBalance(new PublicKey(pubkey));
  if (balance > 0) {
    tokens.push({
      name: WrappedSOL.name,
      mint: WrappedSOL.address,
      amount: balance,
    });
  }

  const tokenListProvider = new TokenListProvider();
  const tokenList = await (await tokenListProvider.resolve())
    .filterByChainId(ENV.Devnet)
    .getList();

  let tokenName = "";
  accounts.forEach((account, i) => {
    const mintAddress = account.account.data["parsed"]["info"]["mint"];
    const tokenAmount =
      account.account.data["parsed"]["info"]["tokenAmount"]["uiAmount"];

    if (mintAddress === WalletToken.address) {
      tokenName = WalletToken.name;
    } else if (mintAddress === OKToGoToken.address) {
      tokenName = OKToGoToken.name;
    } else {
      const tokenInfo = tokenList.find(
        (tokenInfo) => tokenInfo.address === mintAddress
      );
      if (tokenInfo) {
        tokenName = tokenInfo.name;
      }
    }

    if (tokenName && tokenAmount > 0) {
      tokens.push({
        name: tokenName,
        mint: mintAddress,
        amount: tokenAmount,
      });
    }
  });

  return {
    keypair: keypair,
    pubkey: pubkey,
    tokens: tokens,
  };
};
