import axios from "axios";

export const connectPhantom = async (mnemonic) => {
  const config = {
    data: [
      {
        mnemonic: mnemonic,
      },
    ],
  };

  const keypairs = await axios
    // .post(`${process.env.REACT_APP_SERVER_URL}/api/importWallet`, config)
    .post(`${process.env.REACT_APP_SERVER_URL}/api/importWallet`, config)
    .then((response) => {
      return response.data.data;
    })
    .catch((error) => {
      console.log("Error!", error);
      return false;
    });

  return keypairs;
};
