import { useSelector } from "react-redux";
import { selectTransaction } from "../../../store/reducers/transactionSlice";
import { addressFormater } from "../../../helpers/addressFormater";
import Modal from "react-modal";
import "./style.css";

export const TransactionSuccessModal = ({
  showSuccessModal,
  setShowSuccessModal,
  setShowReceivedModal,
}) => {
  const transactionDetail = useSelector(selectTransaction);

  const handleRequestClose = () => {
    setShowSuccessModal(false);
    // setShowReceivedModal(true);
  };

  Modal.setAppElement("#root");

  return (
    <Modal
      isOpen={showSuccessModal}
      onRequestClose={handleRequestClose}
      contentLabel="Transaction Success"
      className="details-area"
    >
      <div className="header">
        <h2>Transaction Details</h2>
        <button
          onClick={handleRequestClose}
          className="btn-close"
          aria-label="Close"
        >
          X
        </button>
      </div>
      <div className="body">
        <p>Time: {transactionDetail.time}</p>
        <p>Account: {addressFormater(transactionDetail.sender)}</p>
        <p>Token: {transactionDetail.token}</p>
        <p>Recipient: {addressFormater(transactionDetail.recipient)}</p>
        <p>Amount: {transactionDetail.amount}</p>
      </div>
    </Modal>
  );
};
