import styled from "styled-components";

const Button = styled.button.attrs((props) => ({
  type: props.type || "button",
}))`
  font-size: 32px;
  line-height: 39px;
  color: #2a3950;
  background-color: #ff9678;
  padding: 12px 36px;
  border: 2px solid #2a3950;
  border-radius: 19px;

  &:hover {
    color: white;
    border-color: white;
  }

  &:active {
    background-color: #ff9678;
  }

  @media (max-width: 1200px) {
    padding: 10px;
  }
`;

export default Button;
