import { useDispatch, useSelector } from "react-redux";
import { inputStep, selectStep } from "../../../store/reducers/stepSlice";

import Button from "../../../components/buttons/Button";
import "./style.css";

const Dashboard = () => {
  const step = useSelector(selectStep);
  const dispatch = useDispatch();

  return (
    <div
      className={`dashboard bg-darkblue animate__animated animate__fadeIn ${
        step === 0 ? "show" : step === 1 ? "show half" : "hide"
      }`}
    >
      <Button
        className="btn-step btn-wallet"
        onClick={() => dispatch(inputStep())}
      >
        Enter a Wallet
      </Button>
      <div className="title">
        <h2 className="ft-white title-com">Lucid Dream Software Inc.</h2>
        <h1 className="title-project">Test Project</h1>
        <a href="https://www.luciddream.com" className="ft-white title-link">
          www.luciddream.com
        </a>
      </div>
    </div>
  );
};

export default Dashboard;
