import axios from "axios";

export const tokenTransfer = async (fromWallet, to, mint, amount) => {
  const config = {
    data: [
      {
        fromWallet: fromWallet,
        to: to,
        mint: mint,
        amount: amount,
      },
    ],
  };

  const transactionInfo = await axios
    .post(`${process.env.REACT_APP_SERVER_URL}/api/sendToken`, config)
    // .post(`${process.env.REACT_APP_LOCAL_SERVER_URL}/api/sendToken`, config)
    .then((response) => {
      return response.data.data;
    })
    .catch((error) => {
      console.log("Error!", error);
      return false;
    });

  return transactionInfo;
};
