import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  resetStep,
  connectStep,
  selectStep,
} from "../../../store/reducers/stepSlice";
import { addAccounts } from "../../../store/reducers/accountsSlice";
import { connectPhantom } from "../../../utils/connectPhantom";
import { getAccountInfo } from "../../../utils/getAccountInfo";

import Button from "../../../components/buttons/Button";
import Input from "../../../components/inputs/Input";
import { CircleLoading } from "../../../components/loading/CircleLoading";
import "./style.css";

const ConnectWallet = () => {
  const step = useSelector(selectStep);
  const dispatch = useDispatch();
  const [isLoading, setIsLoading] = useState(false);

  const initialMnemonicState = new Array(12).fill("");
  const [mnemonic, setMnemonic] = useState(initialMnemonicState);

  const isFormValid = mnemonic.every((value) => value.trim() !== "");
  const handleContinue = () => {
    setMnemonic(initialMnemonicState);
    dispatch(connectStep());
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    setIsLoading(true);
    // const testmnemonic =
    //   "drive clean dismiss network recycle curious glare cycle run peasant plate candy";
    const accounts = await connectPhantom(mnemonic.join(" "))
      // const accounts = await connectPhantom(testmnemonic)
      .then((accounts) => accounts)
      .catch((error) => console.error(error));

    for (const account of accounts) {
      const accountInfo = await getAccountInfo(account);
      if (accountInfo.tokens.length > 0) {
        dispatch(addAccounts(accountInfo));
      }
    }
    setIsLoading(false);

    if (!isFormValid) {
      return;
    }

    handleContinue();
  };

  return (
    <div
      className={`walletImport bg-darkpurple ${step === 1 ? "open" : "close"}`}
    >
      <div className="body">
        {isLoading && <CircleLoading />}
        <div>
          <form onSubmit={handleSubmit}>
            <div className="text">
              <p className="ft-white ft-big">
                Enter your 12-word recovery phrase.
              </p>
              <p className="text-2 ft-white">
                Your recovery phrase is the key to your wallet
              </p>
            </div>
            <div className="inputbox bg-darkblue">
              <div className="row">
                {mnemonic.map((v, i) => (
                  <div className="col-4" key={i}>
                    <Input
                      value={v}
                      id={i + 1}
                      required
                      onChange={(e) => {
                        const newMnemonic = [...mnemonic];
                        newMnemonic[i] = e.target.value;
                        setMnemonic(newMnemonic);
                      }}
                    />
                  </div>
                ))}
              </div>
            </div>
            <div className="button-group d-flex justify-content-between">
              <button
                className="btn-back ft-white"
                onClick={() => dispatch(resetStep())}
              >
                Back
              </button>
              <Button type="submit">Continue</Button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default ConnectWallet;
