import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { resetStep, selectStep } from "../../store/reducers/stepSlice";
import { resetAccounts } from "../../store/reducers/accountsSlice";
import WalletInfo from "./WalletInfo";
import SendToken from "./SendToken";
import { CircleLoading } from "../../components/loading/CircleLoading";
import Button from "../../components/buttons/Button";
import "./style.css";

const TransactionPage = () => {
  const dispatch = useDispatch();
  const step = useSelector(selectStep);
  const [isTxLoading, setIsTxLoading] = useState(false);

  const resetHandler = () => {
    dispatch(resetAccounts());
    dispatch(resetStep());
  };

  return (
    <div
      className={`bg-darkblue animate__animated animate__fadeInLeft transaction ${
        step === 2 ? "open" : "close"
      }`}
    >
      {isTxLoading && <CircleLoading />}
      <Button className="btn-step" onClick={() => resetHandler()}>
        Reset
      </Button>
      <div className="container">
        <WalletInfo />
        <SendToken setIsTxLoading={setIsTxLoading} />
      </div>
    </div>
  );
};

export default TransactionPage;
